<template>
  <div :class="className" class="bg" :style="{height:height,width:width}"></div>
</template>

<script>
  import echarts from 'echarts'
  require('echarts/theme/macarons') // echarts theme

  export default {
    props: {
      className: {
        type: String,
        default: 'chart'
      },
      width: {
        type: String,
        default: '100%'
      },
      height: {
        type: String,
        default: '350px'
      },
      dateData: {
        type: Array
      },
      dateTitel: {
        type: Object
      }
    },
    data() {
      return {
        chart: null
      }
    },
    mounted() {
      console.log(this.dateData)
      this.initChart(this.dateData)
    },
    watch: {
      dateData(curVal, oldVal) {
        this.chart.clear()
        this.initChart(curVal)
      }
    },
    methods: {
      initChart(dateData) {
        this.chart = echarts.init(this.$el, 'macarons')
        this.chart.setOption({
          title: {
            text: this.dateTitel.title,
            subtext: '按照文件查询结果统计',
            x: 'center'
          },
          series: [
            {
              type: 'pie',
              radius: [15, 80],
              center: ['50%', '50%'],
              animationDuration: 2000,
              data: dateData,
              animationEasing: 'cubicInOut'
            }
          ],
          tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b} : {c} ({d}%)'
          },
          legend: {
            left: 'center',
            bottom: '2',
            data: this.dateTitel.titleArray
          }
        })
      }
    }
  }
</script>

<style scoped>
  .bg {
    background: #fff!important;
    margin: 32px 0;
    padding: 25px 0;
  }
</style>



