<template>
  <div class="content-wrap">
    <div class="content">
      <div class="upload_box">
        <el-upload
          class="upload-demo"
          ref="upload"
          action="http://localhost:8081/readFile"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :on-success="successUpload"
          :file-list="fileList"
          :limit="limitNum"
          :auto-upload="false">
          <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
          <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
          <div slot="tip" class="el-upload__tip">只能上传一个文件</div>
        </el-upload>
      </div>

      <div class="table">
        <el-table
          ref="filterTable"
          :data="tableData"
          height="450"
          style="width: 100%;border: 1px solid #b9b8b8;">
          <el-table-column
            prop="ip"
            label="ip">
          </el-table-column>
          <el-table-column
            prop="statusCode"
            width="110"
            label="响应码">
          </el-table-column>
          <el-table-column
            prop="requestMethod"
            width="110"
            label="请求方法">
          </el-table-column>
          <el-table-column
            prop="url"
            width="240"
            label="url">
          </el-table-column>
          <el-table-column
            prop="agent"
            label="浏览器agent">
          </el-table-column>
          <el-table-column
            prop="interviewTime"
            label="访问时间">
          </el-table-column>
          <el-table-column
            prop="timeConsuming"
            width="110"
            label="耗时ip">
          </el-table-column>
        </el-table>
        <el-pagination
          style="margin-top: 10px"
          background
          @current-change="handleCurrentChange"
          :page-size="pageSize"
          layout="prev, pager, next"
          :total="totalNum">
        </el-pagination>
      </div>

      <div>
        <el-row :gutter="40">
          <el-col :xs="24" :sm="24" :lg="12">
            <div class="chart-wrapper">
              <pie-chart v-bind:dateData="checkPlace" v-bind:dateTitel="checkTitel"></pie-chart>
            </div>
          </el-col>
          <el-col :xs="24" :sm="24" :lg="12">
            <div class="chart-wrapper">
              <pie-chart v-bind:dateData="samplePlace" v-bind:dateTitel="sampledateTitel"></pie-chart>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>

  </div>
</template>

<script>
  import axios from 'axios'
  import pieChart from './pieChart'
  export default {
    name: 'dashboard',
    data() {
      return {
        message: '主页',
        limitNum: 1,
        fileList: [],
        tableData: [],
        checkPlace: [
          {value: '0', name: '临时响应'},
          {value: '0', name: '成功'},
          {value: '0', name: '重定向'},
          {value: '0', name: '请求错误'},
          {value: '0', name: '服务器错误'}
          ],
        samplePlace: [
          { value: '0',name:'一天内' },
          { value: '0',name:'一周内' },
          { value: '0',name:'其他' }
        ],
        checkTitel: { title: '响应码比例图', titleArray: ['临时响应', '成功', '重定向', '请求错误', '服务器错误'] },
        sampledateTitel: { title: '访问时间比例图', titleArray: ['一天内', '一周内', '其他'] },
        totalNum: 0,
        pageSize: 20
      }
    },
    components: { pieChart },

    methods: {
      submitUpload() {
        this.$refs.upload.submit()
      },
      successUpload(response) {
        console.log(response)
        this.tableData = response.list
        this.totalNum = response.totalRecord
        axios.post('/sort', {})
          .then((response) => {
            console.log(response.data)
            let code = response.data.code
            let time = response.data.time
            this.checkPlace = [
              {value: code.code1.toString(), name: '临时响应'},
              {value: code.code2.toString(), name: '成功'},
              {value: code.code3.toString(), name: '重定向'},
              {value: code.code4.toString(), name: '请求错误'},
              {value: code.code5.toString(), name: '服务器错误'}
              ]
            this.samplePlace = [
              {value: time.time1.toString(), name: '一天内'},
              {value: time.time2.toString(), name: '一周内'},
              {value: time.time3.toString(), name: '其他'}
            ]
          })
          .catch((error) => {
            console.log(error)
          })
      },
      handleRemove(file, fileList) {
        console.log(file, fileList)
      },
      handlePreview(file) {
        console.log(file)
      },
      handleCurrentChange(val) {
        axios.post('/readFile/'+ val, {})
          .then((response) => {
            console.log(response)
            this.tableData = response.data.list
          })
          .catch((error) => {
            console.log(error)
          })

      }
    }
  }
</script>

<style scoped>
  .content-wrap {
    width: 1500px;
  }
  .upload_box{
    padding: 20px 40px;
    display: inline-block;
    border: 1px solid #b9b8b8;
    margin-bottom: 20px;
  }
  .content{
    width: 1200px;
    margin: 0 auto;
  }
</style>



